import React from 'react'
import './SongRow.css'
function SongRow({ track }) {
  return (
      <div className='songRow'>
          <img className='songRow__album' src={track.album.images[0].url} alt=''/>{/*will get the track album images from the url*/}
          <div className='songRow__info'>
              <h1>{track.name}</h1>
              <p>{track.artists.map((artist) => artist.name).join(', ')} -{' '}
              {track.album.name} {/*will get the artist name and the album name and join them together separated with commas*/}
              </p>
              </div>
        </div>
    )
}

export default SongRow